import React, { Component } from 'react';
import ReactHighcharts from 'react-highcharts';
import axios from 'axios';
import './App.css';
import _ from 'lodash';
import { configheartbeat, configtemperature } from './config.js'

const temp = [];
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
      interval: 0,
      patients: [],
      patientsData: [],
      currentPatientData: [],
      heartbeat: [],
      temperature: [],
      configheartbeat,
      configtemperature
    };
  }
  componentDidMount() {
    axios.get('/patients').then((result) => {
      let data = [];
      data = result.data
      console.log(data);
      this.setState({
        patientsData: data,
      });
    }).then(() => {
      for (let i = 0; i < this.state.patientsData.length; i += 1) {
        temp.push(<button
          className="App-SongButton"
          onClick={() => {
            this.setState({ page: 1, currentPatient: this.state.patientsData[i], configheartbeat: [], configtemperature: [] }, () => { this.getPatientdata(); });
          }}
        >
          {this.state.patientsData[i].name}
        </button>);
      }
      this.setState({
        patients: temp
      });
    }).catch((error) => console.log);
  }
  getPatientdata() {
    let name = this.state.currentPatient.name;
    console.log(name);
    let interval = setInterval(() => {
      axios.get('/getdata?name=' + name).then((response) => {
        let heartbeat = [];
        let temperature = [];
        let temp = response.data;
        let responsedata = temp;
        responsedata = _.orderBy(temp, ['id'], ['asc']);

        if (!(responsedata.length > 50)) {
          configheartbeat.series[0].data = [configheartbeat.series[0].data[0]];
          configtemperature.series[0].data = [configtemperature.series[0].data[0]];
          for (let i = 0; i < responsedata.length; i += 1) {
            configheartbeat.series[0].data.push(responsedata[i].heartbeat);
            configtemperature.series[0].data.push(responsedata[i].temperature);
          }
        }
        else {
          configheartbeat.series[0].data = [configheartbeat.series[0].data[0]];
          configtemperature.series[0].data = [configtemperature.series[0].data[0]];
          for (let i = response.data.length - 50; i < response.data.length; i += 1) {
            configheartbeat.series[0].data.push(responsedata[i].heartbeat);
            configtemperature.series[0].data.push(responsedata[i].temperature);
          }
        }
        console.log(configtemperature.series[0].data);
        this.setState({ configheartbeat, configtemperature });
      });

    }, 1200);
    this.setState({ interval });
  }
  render() {
    const heading = "HealthCare Monitoring";
    if (this.state.page === 0) {
      return (
        <div className="container">
          <div className="App-Heading"><div className="logo"></div><div className="heading">{heading}</div></div>
          <div className="list-of-patients">{'LIST OF PATIENTS :'}</div>
          <div className="App-Body" >
            {this.state.patients}
          </div>
        </div>
      );
    }
    return (
      <div className="container">
        <div className="App-Heading"><div className="logo"></div><div className="heading">{heading}</div></div>
        <div className="App-Body">
          <div className="App-SongName" >{"patient's name :" + this.state.currentPatient.name}</div>
          <div className="App-SongName" >{"patient's email :" + this.state.currentPatient.email}</div>
          <div className="App-SongName" >{"patient's phone number  :" + this.state.currentPatient.phone_number}</div>
          <div className="App-SongName" >{"patient's address :" + this.state.currentPatient.address}</div>
          <div className="charts">
            <div><ReactHighcharts config={this.state.configheartbeat} /></div>
            <div><ReactHighcharts config={this.state.configtemperature} /></div>
          </div>
          <button
            className="App-BackButton"
            onClick={() => {
              this.setState({ page: 0 });
              clearInterval(this.state.interval);
            }}
          >Back
          </button>
        </div>
      </div>
    );
  }
}
export default App;



